import React, { useState } from "react";
import "./styles.css";
import { InputTodo } from "./components/InputTodo";
import { YetTodos } from "./components/YetTodos";
import { DoneTodos } from "./components/DoneTodos";

export const App = () => {
  const [todoText, setTodotext] = useState("");
  const [yetTodos, setYetTodos] = useState(["日記", "音読", "漢字スキル"]);
  const [doneTodos, setDoneTodos] = useState(["計算プリント"]);

  const onChangeTodoText = (e) => setTodotext(e.target.value);

  const onChangeAdd = () => {
    if (todoText === "") return;
    const newTodos = [...yetTodos, todoText];
    setYetTodos(newTodos);
    setTodotext("");
  };

  const onClickDel = (index) => {
    const newTodos = [...yetTodos];
    newTodos.splice(index, 1);
    setYetTodos(newTodos);
  };

  const onClickComp = (index) => {
    const newYetTodos = [...yetTodos];
    newYetTodos.splice(index, 1);
    const newDoneTodos = [...doneTodos, yetTodos[index]];
    setYetTodos(newYetTodos);
    setDoneTodos(newDoneTodos);
  };

  const onClickBack = (index) => {
    const newDoneTodos = [...doneTodos];
    newDoneTodos.splice(index, 1);
    const newYetTodos = [...yetTodos, doneTodos[index]];
    setDoneTodos(newDoneTodos);
    setYetTodos(newYetTodos);
  };

  return (
    <>
      <h1>今日の宿題</h1>
      <InputTodo
        todoText={todoText}
        onChange={onChangeTodoText}
        onClick={onChangeAdd}
      />
      <YetTodos
        yetTodos={yetTodos}
        onClickComp={onClickComp}
        onClickDel={onClickDel}
      />
      <DoneTodos doneTodos={doneTodos} onClickBack={onClickBack} />
    </>
  );
};
