import React from "react";

export const InputTodo = (props) => {
  const { todoText, onChange, onClick } = props;
  return (
    <section className="input">
      <input
        placeholder="宿題を入力してね"
        value={todoText}
        onChange={onChange}
      />
      <button onClick={onClick}>追加</button>
    </section>
  );
};
