import React, { useState } from "react";

export const YetTodos = (props) => {
  const title = useState("これからやる宿題");
  const { yetTodos, onClickComp, onClickDel } = props;
  return (
    <section className="yet">
      <h2>{title}</h2>
      {yetTodos.length <= 0 && <h3>おめでとう＼(^o^)／</h3>}
      <ul>
        {yetTodos.map((todo, index) => {
          return (
            <li>
              <div key={todo} className="list">
                <p>{todo}</p>
                <button onClick={() => onClickComp(index)}>完了</button>
                <button onClick={() => onClickDel(index)}>削除</button>
              </div>
            </li>
          );
        })}
      </ul>
    </section>
  );
};
